<template>
  <div class="col-sm-12">
    <div class="col-sm-12" style="height: auto">
      <div class="col-sm-4"></div>
      <div class="col-sm-4" id=userfiles-useDevInf-打印>
        <label class="col-sm-4"> 票据类型：</label>
        <div class="col-sm-4">
          <v-select :value.sync="selectone" v-model="selectone"
                    :options='billtypes' placeholder='请选择'
                    close-on-select></v-select>
        </div>
      </div>
      <div class="col-sm-4">
        <div class="col-sm-offset-9">
          <a href="#" @click="record">历史记录</a>
        </div>
      </div>
    </div>
    <div class="col-sm-12" v-if="!flag">
      <billxsdmakeinfo :billselect="billselect" :selectone="selectone[0]" v-if="selectone[0]==='销售单'"></billxsdmakeinfo>
      <billqrkmakeinfo :billselect="billselect" :selectone="selectone[0]" v-if="selectone[0]==='安装卡'"></billqrkmakeinfo>
      <billgzkmakeinfo :billselect="billselect" :selectone="selectone[0]" v-if="selectone[0]==='改装卡'"></billgzkmakeinfo>
      <billjlbmakeinfo :billselect="billselect" :selectone="selectone[0]"
                       v-if="selectone[0]==='销售记录表'"></billjlbmakeinfo>
      <billktkmakeinfo :billselect="billselect" :selectone="selectone[0]" v-if="selectone[0]==='开通卡'"></billktkmakeinfo>
    </div>
    <div class="col-sm-12" v-if="flag">
      <billxsdrecordinfo :billselect="billselect" :selectone="selectone[0]"
                         v-if="selectone[0]==='销售单'"></billxsdrecordinfo>
      <billqrkrecordinfo :billselect="billselect" :selectone="selectone[0]"
                         v-if="selectone[0]==='安装卡'"></billqrkrecordinfo>
      <billgzkrecordinfo :billselect="billselect" :selectone="selectone[0]"
                         v-if="selectone[0]==='改装卡'"></billgzkrecordinfo>
      <billjlbrecordinfo :billselect="billselect" :selectone="selectone[0]"
                         v-if="selectone[0]==='销售记录表'"></billjlbrecordinfo>
      <billktkrecordinfo :billselect="billselect" :selectone="selectone[0]"
                         v-if="selectone[0]==='开通卡'"></billktkrecordinfo>
    </div>
  </div>
</template>

<script>
  export default {
    title: "打印票据",
    props: ['billselect'],
    data() {
      return {
        billtypes: this.$appdata.getParam('票据类型'),
        selectone: null,
        flag: false
      }
    },
    methods: {
      // 票据打印历史记录
      record() {
        this.flag = true
      }
    },
    watch: {
      'selectone[0]'() {
        this.flag = false
      }
    }
  }
</script>




